* {
  outline: none;
  border: none;
}
.container {
  width: 100%;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container .elements {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-around;
  width: 550px;
  height: 95%;
  background-color: white;
  border-radius: 30px;
  box-shadow: 0px 10px 13px -7px #000000,
    -1px 9px 27px 39px rgba(31, 36, 23, 0.34);
}
.container .elements div {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
}
.container .elements div input {
  width: 300px;
  height: 25px;
  border-bottom: 1px solid rgb(175, 173, 190);
  font-size: 22px;
}
.container .elements div button{
    width: 130px;
    height: 50px;
    cursor: pointer;
    border-radius: 10px;
    border: 1px solid rgb(167, 150, 150);
}
@media screen and (max-width:600px) {
  .container .elements{
    width: 400px;
  }
  .container .elements div h2{
    font-size: 20px;
  }
  .container .elements div input{
    font-size: 18px;
  }
  .container .elements div button{
    width: 100px;
    height: 30px;
    border-radius: 5px;
  }
}
@media screen and (max-height:800px){
  .container{
    height: 650px;
  }
}
@media screen and (max-height:651px){
  .container{
    height: 550px;
  }
  .container .elements{
    width: 350px;
  }
  .container .elements div h2{
    font-size: 18px;
  }
  .container .elements div input{
    font-size: 15px;
  }
  .container .elements div button{
    width: 80px;
    height: 25px;
  }
}